.all{
    width: 500px;
    margin: 0 auto;
    background: #cdcdcd;
}
h2{
    color: white;
}
.top{
    background: #323232;
    height: 50px;
}
.top input{
    width: 200px;
    height: 30px;
    border-radius: 5px;
    font-size: 15px;
    border: 2px solid grey;
    margin-left: 30px;
    margin-top: 7px;
}
.amount{
    width: 20px;
    height: 20px;
    float: right;
    border-radius: 50%;
    text-align: center;
    line-height: 20px; 
    background: #e6e6fa;
    font-size: 15px;
    margin-right: 100px;
}

li{
    width: 360px;
    height: 35px;
    list-style: none;
    background: white;
    border-left: 5px solid green;
    margin-bottom: 10px;  
}

.choice{
    width: 15px;
    height: 15px;
    margin: 10px;
}

button{
    float: right;
    margin-top: 5px;
    margin-right: 10px;
}
